import React, { Component } from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reset } from "redux-form";
import { NotifSucces } from "../../../components/helpers/notification";
import { Panel, PanelBody, PanelHeader } from "../../../components/panel/panel";
import { postData } from "../../../config/axios";
import HeadTambahBank from "./HeadTambahBank";

class TambahUangBank extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading:false
    };
  }

  handleSubmit(data) {
    this.setState({ isLoading: true });

    let data_post = {
      kategori : data.kategori,
      jumlah_rp: data.jumlah,
      deskripsi: data.keterangan
    }
    postData("cash/in", data_post)
      .then(() => {
        NotifSucces("Data Berhasil Disimpan");
      })
      .then(() => {
        this.setState({ isLoading: false });
      })
      .then(() => {
        this.props.dispatch(reset("HeadTambahBank"));
      })
      .catch((error) =>
        this.ErrorHandling(error)
    );
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Transaksi Bank</Link>
          </li>
          <li className="breadcrumb-item active">Tambah Uang Bank</li>
        </ol>
        <h1 className="page-header">Tambah Uang Bank </h1>
        <Panel>
          <PanelHeader>Tambah Uang Bank</PanelHeader>
          <PanelBody>
            <HeadTambahBank isLoading={this.state.isLoading} onSubmit={(data) => this.handleSubmit(data)} />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}

export default connect()(TambahUangBank);

